.dm_Register {
  background: #fff;
  height: 100vh;
}
.dm_Register .main_content {
  padding: 44px 1rem 0;
}
.dm_Register .main_content .am-list-item.am-input-item {
  padding-left: 0;
}
.dm_Register .main_content .login_btns {
  margin: 2rem 0;
}
.dm_Register .main_content .login_btns span {
  font-size: inherit;
}
.dm_Register .main_content .login_handle {
  margin: 1rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dm_Register .main_content .login_handle a {
  font-size: 14px;
  color: #555;
}
.dm_Register .main_content .err_tip {
  font-size: 14px;
  color: red;
  line-height: 3rem;
}
