.ProductsDetail_Comment {
  padding-top: 1rem;
}
.comment_list .am-list-extra {
  font-size: 13px !important;
}
.comment_list .am-list-line-multiple .am-list-content {
  font-size: 13px;
}
.comment_list .am-list-line-multiple .am-list-content .top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 2rem;
}
.comment_list .am-list-line-multiple .am-list-content .top div,
.comment_list .am-list-line-multiple .am-list-content .top span {
  flex: 1 1;
  min-width: 50%;
}
.comment_list .am-list-line-multiple .am-list-content .top div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}
.comment_list .am-list-line-multiple .am-list-content .top div img {
  width: auto;
  height: 100%;
  margin-right: 0.4rem;
  border-radius: 50%;
  background: #fff;
}
.comment_list .am-list-line-multiple .am-list-content .top span {
  text-align: right;
  color: #555;
}
.comment_list .am-list-line-multiple .am-list-content .bottom {
  font-size: 14px;
  padding: 0.4rem 0 0;
  white-space: normal;
}
.comment_list .all_comment .am-list-line {
  justify-content: center;
}
.comment_list .all_comment .am-list-line .am-list-content {
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.comment_list .all_comment .am-list-line .am-list-content .am-icon {
  vertical-align: middle;
}
.comment_list .no_comment .am-list-content {
  font-size: 12px;
  text-align: center;
}
