.dm_MyCollection {
  width: 100%;
  background: #fff;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content {
  display: flex;
  padding: 1rem;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content > img {
  width: auto;
  height: 7rem;
  margin-right: 1rem;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content {
  line-height: 1;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .spec_list {
  padding: 0.2rem 0.3rem 0.2rem 0.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f0f0f0;
  color: #555;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .spec_list span {
  flex: 1 1;
  line-height: 1.4rem;
  word-break: break-all;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number {
  padding: 0.6rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #1890ff;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number .price span {
  font-size: 16px;
  padding-left: 0.2rem;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number .p_number {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number .p_number .nbr {
  width: 2rem;
  height: 100%;
  line-height: 2rem;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  background: #f0f0f0;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number .p_number .nbr .nbr_disabled {
  background: #fff;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number .p_number .am-list-item.am-input-item {
  height: 100%;
  min-height: 100%;
  padding-left: 0;
  background: #f0f0f0;
  margin: 0 0.2rem;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number .p_number .am-list-item.am-input-item .am-list-line {
  padding-right: 0;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content .content .price_number .p_number .am-list-item.am-input-item .am-list-line input {
  font-size: 12px;
  width: 3rem;
  text-align: center;
}
.dm_MyCollection .dm_cart_ListView .dm_cart_ListView_row_content_line {
  border-bottom: 1px solid #f0f0f0;
}
