.dm_My {
  width: 100%;
}
.dm_My .avatar_info {
  width: 100%;
  height: 14rem;
  background: #1890ff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom-right-radius: 300px 20px;
  border-bottom-left-radius: 300px 20px;
}
.dm_My .avatar_info .avatar {
  width: 6rem;
  height: 6rem;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 50%;
  margin: 1rem auto 0;
  overflow: hidden;
}
.dm_My .avatar_info .avatar img {
  width: 100%;
  height: auto;
}
.dm_My .avatar_info span {
  font-size: 16px;
  color: #fff;
  margin-top: 0.4rem;
  min-height: 2rem;
  line-height: 2rem;
}
.dm_My .dm_My_main_content {
  padding: 1rem;
}
.dm_My .dm_My_main_content .am-list {
  background: #fff;
  border-radius: 0.8rem;
  overflow: hidden;
}
.dm_My .dm_My_main_content .am-list .am-list-body:before,
.dm_My .dm_My_main_content .am-list .am-list-body:after {
  height: 0;
}
.dm_My .dm_My_main_content .am-list .am-list-body .am-list-item:last-child {
  margin-top: 6rem;
}
.dm_My .dm_My_main_content .am-list .am-list-body .am-list-item .am-list-content {
  font-size: 16px;
}
.dm_My .dm_My_main_content .am-list .am-list-body .am-list-item .am-list-arrow {
  width: 12px;
  height: 12px;
}
