html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-family: '微软雅黑', 'Microsoft YaHei';
  font-size: 12px;
  vertical-align: middle;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
  box-sizing: border-box;
  background-color: #f0f0f0 !important;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.dm_App {
  width: 100%;
}
.dm_App .common_width {
  width: 1100px;
  margin: 0 auto;
}
.ant-table {
  color: #666 !important;
}
.ant-select-dropdown-menu-item {
  font-size: 12px !important;
}
.unit {
  font-size: 14px;
}
.ellipsis {
  width: auto;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
a {
  color: inherit;
}
* {
  touch-action: pan-y;
}
.ant-input[disabled] {
  color: inherit;
  cursor: text;
}
.ant-select-disabled {
  color: inherit;
}
.ant-select-disabled .ant-select-selection {
  cursor: text;
}
.ant-input-number-disabled {
  color: inherit;
  cursor: text;
}
.ant-checkbox-disabled + span {
  color: inherit;
  cursor: text;
}
.ant-form-item-with-help {
  margin-bottom: 0 !important;
}
.ant-checkbox-wrapper {
  display: flex;
  align-items: center;
}
.ant-checkbox-wrapper span + span {
  padding-bottom: 2px;
}
.common_bg {
  background: #fff;
  border: solid 1px #e6e6e6;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
}
.ant-upload.ant-upload-select-picture-card {
  margin-right: 0;
  margin-bottom: 0;
}
.ant-collapse .ant-collapse-header {
  font-size: 13px;
  padding: 8px 16px 8px 40px !important;
}
td.columns_left {
  text-align: left !important;
}
.drawer_form .ant-form-item {
  display: flex;
  padding: 2px 0;
}
.drawer_form .ant-form-item .ant-form-item-label {
  width: 90px;
  padding-right: 4px;
}
.drawer_form .ant-form-item .ant-form-item-label label {
  font-size: 12px;
}
.drawer_form .ant-form-item .ant-form-item-control-wrapper {
  flex: 1 1;
}
.drawer_form .ant-form-item .ant-form-item-control-wrapper .ant-input {
  font-size: 12px;
}
.drawer_form .ant-form-item .ant-form-item-control-wrapper .ant-switch {
  border-radius: 100px !important;
}
.drawer_form .ant-form-item.radios {
  padding: 0;
}
.drawer_form .ant-form-item.radios .ant-form-item-control-wrapper {
  display: flex;
  align-items: center;
}
.drawer_form .ant-form-item.radios .ant-form-item-control-wrapper .ant-form-item-control {
  line-height: inherit;
}
.drawer_form .ant-form-item.radios .ant-form-item-control-wrapper .ant-form-item-control .ant-radio-group {
  display: flex;
}
.drawer_form .ant-form-item.radios .ant-form-item-control-wrapper .ant-form-item-control .ant-radio-group .ant-radio {
  vertical-align: middle;
}
.drawer_form .ant-form-item.radios .ant-form-item-control-wrapper .ant-form-item-control .ant-radio-group .ant-radio .ant-radio-inner {
  border-radius: 50% !important;
}
.drawer_form .ant-form-item.selects .ant-form-item-control {
  line-height: 35px;
}
.err_tip {
  font-size: 14px;
  color: red;
  padding: 1rem;
  text-align: left;
}
