.dm_ReceivingAddress .main_content .am-list-body:before {
  height: 0;
}
.dm_ReceivingAddress .main_content .am-list-body .am-list-content .address_info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dm_ReceivingAddress .main_content .am-list-body .am-list-content .address_info span {
  font-size: 14px;
  margin-right: 0.6rem;
}
.dm_ReceivingAddress .main_content .am-list-body .am-list-content .am-list-brief {
  font-size: 12px;
  margin-top: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0.2rem 0;
}
.dm_ReceivingAddress .main_content .am-list-body .am-list-content .am-list-brief i {
  background: #1890ff;
  color: #fff;
  font-style: normal;
  padding: 0 0.4rem;
  margin-right: 0.6rem;
}
.dm_ReceivingAddress .main_content .am-list-body .am-list-extra span {
  font-size: 13px;
}
.dm_ReceivingAddress_modal form .am-list-line .am-input-label {
  font-size: 16px;
}
.dm_ReceivingAddress_modal form .am-list-line .am-input-control input {
  font-size: 16px;
}
.dm_ReceivingAddress_modal form .err_tip {
  padding: 0 1rem;
  background: #fff;
}
