.dm_SettlementPage {
  width: 100%;
}
.dm_SettlementPage .address .am-list .am-list-body:before {
  height: 0;
}
.dm_SettlementPage .address .am-list .am-list-body .am-list-content .address_info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dm_SettlementPage .address .am-list .am-list-body .am-list-content .address_info span {
  font-size: 14px;
  margin-right: 0.6rem;
}
.dm_SettlementPage .address .am-list .am-list-body .am-list-content .am-list-brief {
  font-size: 12px;
  margin-top: 0;
}
.dm_SettlementPage .p_details,
.dm_SettlementPage .common_info {
  background: #fff;
}
.dm_SettlementPage .p_details {
  padding: 0 1rem;
  border-bottom: 1px solid #f0f0f0;
}
.dm_SettlementPage .p_details .dm_OrderDetails_content {
  display: flex;
  padding: 1rem 0;
}
.dm_SettlementPage .p_details .dm_OrderDetails_content > img {
  height: 7rem;
  margin-right: 1rem;
}
.dm_SettlementPage .p_details .dm_OrderDetails_content .content {
  width: 100%;
  overflow: hidden;
}
.dm_SettlementPage .p_details .dm_OrderDetails_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_SettlementPage .p_details .dm_OrderDetails_content .content .spec_list {
  line-height: 1.2rem;
  color: #555;
  padding: 0.2rem 0;
}
.dm_SettlementPage .p_details .dm_OrderDetails_content .content .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #555;
  padding: 0.6rem 0;
}
.dm_SettlementPage .p_details .dm_OrderDetails_content .content .price span {
  font-size: 14px;
  margin: 0 0.4rem 0 0.2rem;
}
.dm_SettlementPage .p_details .dm_OrderDetails_content .content .price i {
  font-style: normal;
}
.dm_SettlementPage .common_info {
  line-height: 2rem;
  padding: 1rem;
}
.dm_SettlementPage .common_info div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dm_SettlementPage .common_info div span {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dm_SettlementPage .common_info div span i {
  font-style: normal;
}
.dm_SettlementPage .common_info div.common_info_special span {
  color: #1890ff;
  font-size: 16px;
}
.dm_SettlementPage .common_info div.common_info_special span i {
  font-size: 12px;
}
.dm_SettlementPage .bottom_tab_btns {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 50px;
  line-height: 50px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  z-index: 999;
}
.dm_SettlementPage .bottom_tab_btns div,
.dm_SettlementPage .bottom_tab_btns span {
  height: 100%;
  text-align: center;
  font-size: 16px;
  overflow: hidden;
}
.dm_SettlementPage .bottom_tab_btns span {
  background: #1890ff;
  color: #fff;
  width: 40%;
}
.dm_SettlementPage .bottom_tab_btns div {
  max-width: 60%;
  flex: 1 1;
  color: #1890ff;
  font-size: 26px;
}
.dm_SettlementPage .bottom_tab_btns div i {
  font-style: normal;
  vertical-align: baseline;
  font-size: 12px;
  font-weight: bold;
}
.dm_SettlementPage_main_content .am-list .am-list-body:before {
  height: 0;
}
.dm_SettlementPage_main_content .am-list .am-list-body .am-list-content .address_info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dm_SettlementPage_main_content .am-list .am-list-body .am-list-content .address_info span {
  font-size: 14px;
  margin-right: 0.6rem;
}
.dm_SettlementPage_main_content .am-list .am-list-body .am-list-content .am-list-brief {
  font-size: 12px;
  margin-top: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 0.2rem 0;
}
.dm_SettlementPage_main_content .am-list .am-list-body .am-list-content .am-list-brief i {
  background: #1890ff;
  color: #fff;
  font-style: normal;
  padding: 0 0.4rem;
  margin-right: 0.6rem;
}
