.dm_OrderDetails {
  width: 100%;
}
.dm_OrderDetails .address .am-list .am-list-thumb {
  margin-right: 1rem;
}
.dm_OrderDetails .address .am-list .am-list-thumb img {
  width: 16px;
  height: auto;
}
.dm_OrderDetails .address .am-list .am-list-content .address_info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dm_OrderDetails .address .am-list .am-list-content .address_info span {
  font-size: 14px;
  margin-right: 0.6rem;
}
.dm_OrderDetails .address .am-list .am-list-content .am-list-brief {
  font-size: 12px;
  margin-top: 0;
}
.dm_OrderDetails .p_details,
.dm_OrderDetails .common_info {
  background: #fff;
}
.dm_OrderDetails .p_details {
  padding: 0 1rem;
  border-bottom: 1px solid #f0f0f0;
}
.dm_OrderDetails .p_details .dm_OrderDetails_content {
  display: flex;
  padding: 1rem 0;
}
.dm_OrderDetails .p_details .dm_OrderDetails_content > img {
  height: 7rem;
  margin-right: 1rem;
}
.dm_OrderDetails .p_details .dm_OrderDetails_content .content {
  width: 100%;
  overflow: hidden;
}
.dm_OrderDetails .p_details .dm_OrderDetails_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_OrderDetails .p_details .dm_OrderDetails_content .content .spec_list {
  line-height: 1.2rem;
  color: #555;
  padding: 0.2rem 0;
}
.dm_OrderDetails .p_details .dm_OrderDetails_content .content .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #555;
  padding: 0.6rem 0;
}
.dm_OrderDetails .p_details .dm_OrderDetails_content .content .price span {
  font-size: 14px;
  margin: 0 0.4rem 0 0.2rem;
}
.dm_OrderDetails .p_details .dm_OrderDetails_content .content .price i {
  font-style: normal;
}
.dm_OrderDetails .common_info {
  line-height: 2rem;
  padding: 1rem;
  color: #555;
}
.dm_OrderDetails .common_info div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dm_OrderDetails .common_info div span {
  display: flex;
  justify-content: center;
  align-items: center;
}
.dm_OrderDetails .common_info div span i {
  font-style: normal;
}
.dm_OrderDetails .common_info div.common_info_special span {
  color: #1890ff;
  font-size: 16px;
}
.dm_OrderDetails .common_info div.common_info_special span i {
  font-size: 12px;
}
