.dm_MyEvaluation {
  width: 100%;
}
.dm_MyEvaluation .content {
  padding: 0 1rem;
  background: #fff;
}
.dm_MyEvaluation .content .p_details {
  background: #fff;
}
.dm_MyEvaluation .content .p_details .dm_OrderDetails_content {
  display: flex;
  padding: 1rem 0;
}
.dm_MyEvaluation .content .p_details .dm_OrderDetails_content > img {
  height: 7rem;
  margin-right: 1rem;
}
.dm_MyEvaluation .content .p_details .dm_OrderDetails_content .content {
  width: 100%;
  overflow: hidden;
}
.dm_MyEvaluation .content .p_details .dm_OrderDetails_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_MyEvaluation .content .p_details .dm_OrderDetails_content .content .spec_list {
  line-height: 1.2rem;
  color: #555;
  padding: 0.2rem 0;
}
.dm_MyEvaluation .content .p_details .dm_OrderDetails_content .content .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #555;
  padding: 0.6rem 0;
}
.dm_MyEvaluation .content .p_details .dm_OrderDetails_content .content .price span {
  font-size: 14px;
  margin: 0 0.4rem 0 0.2rem;
}
.dm_MyEvaluation .content .p_details .dm_OrderDetails_content .content .price i {
  font-style: normal;
}
.dm_MyEvaluation .content .comment_content {
  background: #f0f0f0;
  border-radius: 0.2rem;
  padding-right: 14px;
}
.dm_MyEvaluation .content .comment_content .am-textarea-control textarea {
  font-size: 14px;
}
.dm_MyEvaluation .content .comment_content .am-textarea-count {
  font-size: 12px;
  display: flex;
}
.dm_MyEvaluation .content .err_tip {
  color: red;
  height: 2rem;
  line-height: 2rem;
}
