.dm_HotThisWeek .title {
  text-align: center;
  font-size: 16px;
  padding: 1rem;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dm_HotThisWeek .title img {
  width: 6%;
  height: auto;
  margin-right: 0.2rem;
}
.dm_HotThisWeek .am-grid {
  background: #fff;
  padding-bottom: 2rem;
  min-height: 384px;
}
.dm_HotThisWeek .am-grid .slider-decorator-0 {
  bottom: -2rem !important;
  background: #fff;
  border-top: 1px solid #F7F7F7;
}
.dm_HotThisWeek .am-grid .am-grid-item-content .dm_HotThisWeek_products {
  height: inherit;
  padding: 0 1rem;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.dm_HotThisWeek .am-grid .am-grid-item-content .dm_HotThisWeek_products img {
  width: 70%;
}
.dm_HotThisWeek .am-grid .am-grid-item-content .dm_HotThisWeek_products div {
  text-align: left;
}
.dm_HotThisWeek .am-grid .am-grid-item-content .dm_HotThisWeek_products div span {
  text-align: left;
  word-break: break-all;
  line-height: 1.4rem;
  font-size: 13px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_HotThisWeek .am-grid .am-grid-item-content .dm_HotThisWeek_products div p {
  font-size: 15px;
  padding: 0.4rem 0;
  font-weight: 700;
}
.dm_HotThisWeek .am-grid .am-grid-item-content .dm_HotThisWeek_products div p:before {
  content: '￥';
  font-size: 12px;
  color: #1890ff;
  font-weight: normal;
}
