.dm_cart {
  width: 100%;
  height: 100vh;
  background: #fff;
}
.dm_cart .empty_cart {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 2.6rem;
  font-size: 14px;
  color: #555;
}
.dm_cart .empty_cart img {
  width: 2.6rem;
  height: auto;
  margin-right: 0.8rem;
}
.dm_cart .dm_cart_header {
  padding: 0.96rem 1rem 0.4rem;
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  top: 0;
  z-index: 999;
  background: #fff;
  border-bottom: 1px solid #f0f0f0;
}
.dm_cart .dm_cart_header .top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dm_cart .dm_cart_header .top h1 {
  font-size: 20px;
}
.dm_cart .dm_cart_header .top h1 .am-icon {
  vertical-align: sub;
  margin-right: 0.4rem;
  margin-left: 0.26rem;
}
.dm_cart .dm_cart_header .top span {
  font-size: 15px;
}
.dm_cart .dm_cart_header .bottom {
  width: 100%;
  line-height: 3rem;
  display: flex;
}
.dm_cart .dm_cart_header .bottom span {
  color: #555;
  display: inline-block;
}
.dm_cart .dm_cart_header .bottom span:first-child {
  flex: 1 1;
}
.dm_cart .dm_cart_header .bottom span:last-child {
  flex: 3 1;
}
.dm_cart .dm_cart_footer {
  width: 100%;
  position: fixed;
  left: 0;
  z-index: 99;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
  border-top: 1px solid #f0f0f0;
  height: 3.5rem;
  line-height: 3.5rem;
  overflow: hidden;
}
.dm_cart .dm_cart_footer .left .am-checkbox-agree-label {
  font-size: 14px;
  margin-top: -1px;
}
.dm_cart .dm_cart_footer .left .am-checkbox-agree-label .am-checkbox .am-checkbox-inner {
  top: 8px;
}
.dm_cart .dm_cart_footer .right {
  flex: 1 1;
  max-width: 70%;
  height: 100%;
  display: flex;
}
.dm_cart .dm_cart_footer .right.right_manage {
  justify-content: flex-end;
}
.dm_cart .dm_cart_footer .right span {
  display: inline-block;
  flex: 1 1;
  text-align: right;
  font-size: 14px;
  padding-right: 1rem;
}
.dm_cart .dm_cart_footer .right span i {
  font-style: normal;
  font-weight: bold;
  color: #1890ff;
  font-size: inherit;
}
.dm_cart .dm_cart_footer .right > i {
  min-width: 8rem;
  height: 100%;
  color: #fff;
  font-style: normal;
  font-size: 14px;
  text-align: center;
}
.dm_cart .dm_cart_footer .right > i:first-child {
  background: #1890ff;
}
.dm_cart .dm_cart_footer .right > i:last-child {
  background: #0e80d2;
}
.dm_cart .dm_cart_ListView {
  height: 100vh;
}
.dm_cart .dm_cart_ListView .am-list-body:before,
.dm_cart .dm_cart_ListView .am-list-body:after {
  height: 0;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row.am-list-item-active {
  background: #f0f0f0;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-thumb {
  margin-right: 0.4rem;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content {
  white-space: normal;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content {
  display: flex;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content > img {
  width: auto;
  height: 7rem;
  margin-right: 1rem;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content {
  line-height: 1;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .spec_list {
  padding: 0.2rem 0.3rem 0.2rem 0.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f0f0f0;
  color: #555;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .spec_list span {
  flex: 1 1;
  line-height: 1.4rem;
  word-break: break-all;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .spec_list .am-icon {
  width: 1.3rem;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number {
  padding: 0.6rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #1890ff;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number .price span {
  font-size: 16px;
  padding-left: 0.2rem;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number .p_number {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 2rem;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number .p_number .nbr {
  width: 2rem;
  height: 100%;
  line-height: 2rem;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  background: #f0f0f0;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number .p_number .nbr .nbr_disabled {
  background: #fff;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number .p_number .am-list-item.am-input-item {
  height: 100%;
  min-height: 100%;
  padding-left: 0;
  background: #f0f0f0;
  margin: 0 0.2rem;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number .p_number .am-list-item.am-input-item .am-list-line {
  padding-right: 0;
}
.dm_cart .dm_cart_ListView .am-list-body .dm_cart_ListView_row .am-list-content .dm_cart_ListView_row_content .content .price_number .p_number .am-list-item.am-input-item .am-list-line input {
  font-size: 12px;
  width: 3rem;
  text-align: center;
}
.dm_cart .dm_cart_ListView .dm_cart_ListView_row_content_line {
  border-bottom: 1px solid #f0f0f0;
}
.dm_cart_active_spec .am-action-sheet-button-list-item {
  font-size: 13px;
  height: auto;
  line-height: 3.6rem;
}
.dm_cart_active_spec .am-action-sheet-button-list-item:last-child {
  font-size: 16px;
}
.dm_cart_active_spec .dm_cart_showActionSheet_active {
  background: #1890ff;
  color: #fff;
}
