.dm_Message {
  width: 100%;
  height: 100%;
  background: #fff;
  border: solid 1px #e6e6e6;
  box-shadow: 0 1px 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 42px !important;
  padding: 0 20px;
}
.dm_Message .ant-comment {
  border-top: 1px solid #eee;
}
.dm_Message .submit_comment {
  border-top: none;
}
.dm_Message .submit_comment .ant-comment-avatar,
.dm_Message .submit_comment .ant-comment-content-author {
  display: none;
}
.dm_Message .submit_comment .submit {
  text-align: right;
  padding-top: 16px;
}
