.dm_MyOrder {
  width: 100%;
  background: #fff;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row {
  padding: 0 1rem;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .dm_MyOrder_ListView_row_content {
  display: flex;
  padding: 1rem 0;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .dm_MyOrder_ListView_row_content > img {
  height: 7rem;
  margin-right: 1rem;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .dm_MyOrder_ListView_row_content .content {
  width: 100%;
  overflow: hidden;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .dm_MyOrder_ListView_row_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .dm_MyOrder_ListView_row_content .content .spec_list {
  line-height: 1.2rem;
  color: #555;
  padding: 0.2rem 0;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .dm_MyOrder_ListView_row_content .content .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #555;
  padding: 0.6rem 0;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .dm_MyOrder_ListView_row_content .content .price span {
  font-size: 14px;
  margin: 0 0.4rem 0 0.2rem;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .dm_MyOrder_ListView_row_content .content .price i {
  font-style: normal;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .bottom_btns .p_info {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .bottom_btns .p_info > span {
  margin-right: 0.6rem;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .bottom_btns .p_info > div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .bottom_btns .p_info > div span + span {
  font-size: 12px;
  margin-right: 0.2rem;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .bottom_btns .p_info > div i {
  font-style: normal;
  font-size: 16px;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .bottom_btns .btns {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 1rem 0;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .bottom_btns .btns a {
  margin-left: 1rem;
  height: 2.5rem;
  line-height: 2.5rem;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row .bottom_btns .btns a span {
  vertical-align: bottom;
}
.dm_MyOrder .dm_MyOrder_ListView .dm_MyOrder_ListView_row_content_line {
  border-bottom: 0.6rem solid #f0f0f0;
}
.dm_MyOrder_modal .modal_main_content .p_details {
  padding: 0 1rem;
  border-bottom: 1px solid #f0f0f0;
  background: #fff;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content {
  display: flex;
  padding: 1rem 0;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content > img {
  height: 7rem;
  margin-right: 1rem;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content {
  width: 100%;
  overflow: hidden;
  text-align: left;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  color: #000;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content .spec_list {
  line-height: 1.2rem;
  color: #555;
  padding: 0.2rem 0;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content .price_and_btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0.4rem 0;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content .price_and_btn .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #555;
  padding: 0.6rem 0;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content .price_and_btn .price span {
  font-size: 14px;
  margin: 0 0.4rem 0 0.2rem;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content .price_and_btn .price i {
  font-style: normal;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content .price_and_btn a {
  margin-left: 1rem;
  height: 2.5rem;
  line-height: 2.5rem;
}
.dm_MyOrder_modal .modal_main_content .p_details .dm_OrderDetails_content .content .price_and_btn a span {
  vertical-align: bottom;
}
