}
.dm_UserCenter .am-list .am-list-body:before {
  height: 0;
}
.dm_UserCenter .am-list .am-list-body .am-list-item {
  min-height: 2rem;
}
.dm_UserCenter .am-list .am-list-body .am-list-item .am-list-content {
  font-size: 16px;
  line-height: 2.6rem;
}
.dm_UserCenter .am-list .am-list-body .am-list-item .am-list-arrow {
