.dm_GridList {
  padding: 0.6rem 0;
  background: #fff;
}
.dm_GridList .am-flexbox .am-grid-item-content {
  padding: 0;
}
.dm_GridList .am-flexbox .am-grid-item-content .dm_GridList_content {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.dm_GridList .am-flexbox .am-grid-item-content .dm_GridList_content .brand {
  width: 4rem;
  height: 4rem;
  margin: 0 auto;
  border-radius: 50%;
  background: #F0F0F0;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
