.Stepper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 116px !important;
  height: 30px;
}
.Stepper .am-stepper-handler-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  height: inherit;
  z-index: 55;
}
.Stepper .am-stepper-handler-wrap span {
  border: 0 none;
  background: #F7F7F7;
  border-radius: 3px;
}
.Stepper .am-stepper-handler-wrap span .am-icon {
  vertical-align: middle;
}
.Stepper .am-stepper-input-wrap {
  width: 48px;
  background: #F7F7F7;
  position: relative;
  z-index: 66;
}
.Stepper .am-stepper-input-wrap .am-stepper-input {
  width: 100%;
}
