.Products_ProductsDetail .big_img img {
  width: 100%;
  height: auto;
}
.Products_ProductsDetail .content {
  background: #fff;
  padding: 0 1rem;
}
.Products_ProductsDetail .content .price {
  padding-top: 1rem;
  color: #1890ff;
}
.Products_ProductsDetail .content .price i {
  font-style: normal;
  font-size: 16px;
}
.Products_ProductsDetail .content .price span {
  font-size: 26px;
}
.Products_ProductsDetail .content .product_info {
  border-bottom: 1px solid #f0f0f0;
  padding: 1rem 0;
  word-break: break-all;
}
.Products_ProductsDetail .content .product_info p {
  font-size: 16px;
  font-weight: 700;
  line-height: 2rem;
}
.Products_ProductsDetail .content .product_info span {
  padding: 0.4rem 0;
  color: #555;
}
.Products_ProductsDetail .content .num {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 0;
}
.Products_ProductsDetail .content .product_spec .am-list-item {
  padding-left: 0;
}
.Products_ProductsDetail .content .product_spec .am-list-item .am-list-line {
  padding-right: 0;
}
.Products_ProductsDetail .content .product_spec .am-list-item .am-list-line .am-list-content {
  font-size: 12px;
  min-width: 5rem;
  flex: none;
}
.Products_ProductsDetail .content .product_spec .am-list-item .am-list-line .am-list-extra {
  font-size: 14px;
  flex: 1 1;
}
