.dm_HeaderBar {
  display: flex;
  width: 100%;
  min-height: 44px;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  background: #f0f0f0;
}
.dm_HeaderBar .logo {
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dm_HeaderBar .logo > img {
  width: 64%;
}
.dm_HeaderBar .search_bar {
  flex: 1 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dm_HeaderBar .search_bar .am-search {
  padding: 0;
  background: #f0f0f0;
  flex: 1 1;
}
.dm_HeaderBar .search_bar .am-search .am-search-input {
  border: 1px solid #e5e5e5;
}
.dm_HeaderBar .search_bar .am-search .am-search-input .am-search-synthetic-ph {
  line-height: 22px;
}
.dm_HeaderBar .search_bar .am-search .am-search-input .am-search-synthetic-ph .am-search-synthetic-ph-container > i {
  background-size: 14px auto;
  background-position: center;
  vertical-align: middle;
}
.dm_HeaderBar .search_bar .am-search .am-search-input .am-search-synthetic-ph .am-search-synthetic-ph-container span {
  font-size: 12px;
}
.dm_HeaderBar .search_bar .am-search .am-search-input .am-search-clear {
  background-size: 14px auto;
}
.dm_HeaderBar .search_bar .am-search .am-search-cancel {
  font-size: 14px;
  width: 4rem;
  text-align: center;
  padding: 0;
}
.dm_HeaderBar .search_bar .right_content {
  width: 48px;
  font-size: 14px;
  color: #1890ff;
  text-align: center;
}
.dm_HeaderBar .search_bar_two .am-search {
  padding-left: 0.7rem;
}
.dm_HeaderBar_navBar {
  background: #f0f0f0 !important;
  height: 44px !important;
  border-bottom: 0 none !important;
}
.dm_HeaderBar_navBar .am-navbar-left {
  padding-left: 0;
  width: 48px;
  justify-content: center;
  flex: none;
}
.dm_HeaderBar_navBar .am-navbar-title {
  width: calc(100% - 48px);
}
.dm_HeaderBar_navBar .am-navbar-title .am-search {
  width: 100%;
  padding: 0;
  background: #f0f0f0;
}
.dm_HeaderBar_navBar .am-navbar-title .am-search .am-search-input {
  border: 1px solid #e5e5e5;
}
.dm_HeaderBar_navBar .am-navbar-title .am-search .am-search-input .am-search-synthetic-ph {
  line-height: 22px;
}
.dm_HeaderBar_navBar .am-navbar-title .am-search .am-search-input .am-search-synthetic-ph .am-search-synthetic-ph-container > i {
  background-size: 14px auto;
  background-position: center;
  vertical-align: middle;
}
.dm_HeaderBar_navBar .am-navbar-title .am-search .am-search-input .am-search-synthetic-ph .am-search-synthetic-ph-container span {
  font-size: 12px;
}
.dm_HeaderBar_navBar .am-navbar-title .am-search .am-search-input .am-search-clear {
  background-size: 14px auto;
}
.dm_HeaderBar_navBar .am-navbar-title .am-search .am-search-cancel {
  font-size: 14px;
  width: 4rem;
  text-align: center;
  padding: 0;
}
.dm_HeaderBar_navBar .am-navbar-right {
  display: none;
}
.modal_main_content .no_result {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-top: 4rem;
}
.modal_main_content .no_result .icon {
  background: #f0f0f0;
  width: 8rem;
  height: 8rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}
.modal_main_content .no_result .icon .am-icon {
  color: #fff;
  width: 60%;
  height: 60%;
}
.modal_main_content .no_result span {
  line-height: 3rem;
}
.modal_main_content .listView_row .listView_row_content {
  display: flex;
  padding: 1rem;
}
.modal_main_content .listView_row .listView_row_content > img {
  height: 7rem;
  margin-right: 1rem;
}
.modal_main_content .listView_row .listView_row_content .content {
  line-height: 1;
}
.modal_main_content .listView_row .listView_row_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.modal_main_content .listView_row .listView_row_content .content .tag_list {
  padding: 0.2rem 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.modal_main_content .listView_row .listView_row_content .content .tag_list .am-tag {
  margin-right: 0.4rem;
  background: #f0f0f0;
  overflow: hidden;
}
.modal_main_content .listView_row .listView_row_content .content .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #1890ff;
  padding: 0.6rem 0;
}
.modal_main_content .listView_row .listView_row_content .content .price span {
  font-size: 16px;
  padding-left: 0.2rem;
}
