.dm_Products {
  width: 100%;
  background: #fff;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row .dm_Products_ListView_row_content {
  display: flex;
  padding: 1rem;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row .dm_Products_ListView_row_content > img {
  height: 7rem;
  margin-right: 1rem;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row .dm_Products_ListView_row_content .content {
  line-height: 1;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row .dm_Products_ListView_row_content .content .description {
  font-size: 14px;
  word-break: break-all;
  line-height: 1.6rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row .dm_Products_ListView_row_content .content .tag_list {
  padding: 0.2rem 0;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row .dm_Products_ListView_row_content .content .tag_list .am-tag {
  margin-right: 0.4rem;
  background: #f0f0f0;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row .dm_Products_ListView_row_content .content .price {
  display: flex;
  align-items: baseline;
  font-size: 12px;
  color: #1890ff;
  padding: 0.6rem 0;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row .dm_Products_ListView_row_content .content .price span {
  font-size: 16px;
  padding-left: 0.2rem;
}
.dm_Products .dm_Products_ListView .dm_Products_ListView_row_content_line {
  border-bottom: 1px solid #f0f0f0;
}
.dm_Products_modal .dm_Products_modal_content {
  width: 100%;
  height: calc(100% - 96px);
  overflow: scroll;
}
.dm_Products_modal .dm_Products_modal_content .am-list .am-list-body:before {
  height: 0;
}
.dm_Products_modal .dm_Products_modal_content .am-list .am-list-body:after {
  background: #f0f0f0;
}
.dm_Products_modal .dm_Products_modal_content .am-list .am-list-body .title .am-list-content {
  font-size: 16px;
}
.dm_Products_modal .dm_Products_modal_content .am-list .am-list-body .content .am-list-content .am-tag {
  width: calc(100% / 3);
}
.dm_Products_modal .dm_Products_modal_content .am-list .am-list-body .content .am-list-content .am-tag .am-tag-text {
  font-size: inherit;
}
.dm_Products_modal .dm_Products_modal_content .am-list .am-list-body .clear_filter .am-list-content {
  font-size: 14px;
  color: #555;
  text-align: center;
}
.dm_Products_modal .am-modal-footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 999;
  background: #fff;
}
.dm_Products_modal .am-modal-footer a {
  font-size: 16px;
}
.dm_Products_modal .am-modal-footer a + a {
  background: #1890ff;
  color: #fff;
}
.dm_Products_modal .am-modal-footer a + a:before {
  height: 0;
}
